.loader-back{
    position: fixed;
    width: 100%;
    height: 100vh;
    background-color: #151515ec;
    z-index: 1000;
    visibility: hidden;
}

.spinner{
    visibility: hidden;
    position: absolute;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    z-index: 1000;
  
}
.spinner::before,
.spinner::after{
    content: "";
    position: absolute;
    border-radius: inherit;    
}
.spinner:before{
    width: 100%;
    height: 100%;
    background-image: linear-gradient(0deg,#ff00cc 0%,#333399 100%);
    animation: spin .5s infinite linear;
}
.spinner:after{
    width: 85%;
    height: 85%;
    background-color: #151515;
    top:50%;
    left: 50%;
    transform: translate(-50%,-50%);
}
@keyframes spin{
    to{
        transform: rotate(360deg);
    }
}